






















import Vue from "vue";

export default Vue.extend({
  data() {
    return {
      visible: false,
      hashes: ["0x12", "02sdf"]
    };
  },
  methods: {
    show(options: any) {
      this.hashes = options.hashes || [];
      this.visible = true;
    },
    hide() {
      this.visible = false;
      this.hashes = [];
    },
    jumpEtherScan(hash: string) {
      const url = `https://ropsten.etherscan.io/tx/${hash}`;
      window.open(url, "_blank");
    }
  }
});
